
























































































































import { Component, Vue, Watch } from "vue-property-decorator";
import AddType from "./components/AddType.vue";
import AddBaseType from "./components/AddBaseType.vue";

import Url from "../../api";
import utils from "@/utils/utils";
import { formatterAttrType } from "@/utils/dataFormat";

@Component({
  components: {
    AddType,
    AddBaseType
  },
  filters: {
    formatterAttrType
  }
})
export default class BaseTypeManage extends Vue {
  oparation: any = "add"; //保存操作，add：添加，edit：编辑
  selectIdx: any = 0;
  typeList: Array<any> = []; //基础属性类型列表
  //当前选中的基础属性类型
  baseTypeSelect: any = {
    id: "",
    baseTypeName: "",
    sort: ""
  };

  // 基础属性类型弹窗
  typeTitle = "";
  typeVisibleDialog = false;
  typeFormData = {
    id: "",
    code: "",
    baseTypeName: "",
    sort: 1
  };

  //自定义属性类型
  itemOptions: Array<any> = [
    {
      value: "select",
      label: "单项选择"
    },
    {
      value: "multiSelect",
      label: "多项选择"
    },
    {
      value: "number",
      label: "数值"
    },
    {
      value: "image",
      label: "图片"
    },
    {
      value: "text",
      label: "文本"
    },
    {
      value: "location",
      label: "定位"
    },
    {
      value: "annex",
      label: "附件"
    }
  ];

  tableList: Array<any> = []; //基础属性表格数据
  loading = false;
  searchDTO: any = {
    current: 1,
    size: 10,
    total: 0,
    query: {
      baseTypeId: "",
      attrType: ""
    }
  };

  // 自定义属性弹窗相关
  visibleDialog: Boolean = false; //弹窗是否显示
  dialogTitle: string = ""; //弹窗标题

  //选中的自定义属性
  attrCur: any = {
    id: "",
    baseTypeId: "",
    code: "",
    attrName: "",
    attrType: "",
    sort: "",
    dataRange: {
      options: [],
      multiSelectedItems: []
    }
  };

  //获取基础属性类型列表
  getBaseTypeList() {
    this.$http.get(Url.getBaseTypeList).then((res: any) => {
      if (res.data.status) {
        this.typeList = res.data.data;
        if (this.typeList.length) {
          this.baseTypeSelect = this.typeList[this.selectIdx];
          this.searchDTO.query.baseTypeId = this.baseTypeSelect.id;
          this.getAttrsList();
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  //基础属性类型点击事件，渲染自定义属性数据
  baseTypeClick(baseType: any, index: any) {
    this.selectIdx = index;
    this.baseTypeSelect = baseType;
    this.searchDTO.query.baseTypeId = baseType.id;
    this.getAttrsList();
  }

  //点击添加基础属性类型事件
  addBaseType() {
    this.typeTitle = "添加基础属性类型";
    this.typeFormData = {
      id: "",
      code: "",
      baseTypeName: "",
      sort: 1
    };
    this.typeVisibleDialog = true;
  }

  //点击编辑基础属性类型事件
  editBaseType() {
    this.typeTitle = "编辑基础属性类型";
    utils.mapValue(this.typeFormData, this.baseTypeSelect);
    this.typeVisibleDialog = true;
  }

  //点击删除基础属性类型事件
  deleteBaseType() {
    let _this = this;
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(Url.deleteBaseType + "/" + this.baseTypeSelect.id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              //重新获取列表
              _this.getBaseTypeList();
            } else {
              _this.$message({
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  //保存基础属性类型
  handleAddSubmit(form: any, selected: Array<any>) {
    let url = form.id ? Url.updateBaseType : Url.addBaseType;
    form.attrs = selected;
    this.$http.post(url, form).then((res: any) => {
      if (res.data.status) {
        this.$message.success("操作成功");
        this.typeVisibleDialog = false;
        this.getBaseTypeList();
      } else {
        this.$message({
          message: res.data.message,
          type: "error"
        });
      }
    });
  }

  //关闭基础属性类型编辑窗口
  typeCloseDialog() {
    this.typeVisibleDialog = false;
  }

  /**
   * 自定义属性相关
   */
  //获取基础属性类型对应的自定义属性列表
  search() {
    this.searchDTO.current = 1;
    this.getAttrsList();
  }
  getAttrsList() {
    this.loading = true;
    this.$http
      .post(Url.getBaseTypeAttrsList, this.searchDTO)
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          const result = res.data.data;
          this.tableList = result.records;
          this.searchDTO.total = result.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  handleCurrentChange(val: number) {
    this.searchDTO.current = val;
    this.getAttrsList();
  }
  handleSizeChange(val: number) {
    this.searchDTO.size = val;
    this.getAttrsList();
  }

  closeDialog() {
    this.visibleDialog = false;
  }

  //添加自定义属性
  attrAdd() {
    if (!this.baseTypeSelect.id) {
      this.$message.error("尚未选择基础属性类型");
      return;
    }
    this.dialogTitle = "添加" + this.baseTypeSelect.baseTypeName + "数据元";
    this.attrCur = {
      id: "",
      baseTypeId: this.baseTypeSelect.id,
      code: "",
      attrName: "",
      attrType: "",
      sort: "",
      dataRange: {
        options: [],
        multiSelectedItems: []
      }
    };
    this.visibleDialog = true;
  }

  //编辑自定义属性
  attrEdit(attr: object) {
    this.dialogTitle = "编辑" + this.baseTypeSelect.baseTypeName + "数据元";
    utils.mapValue(this.attrCur, attr);
    this.attrCur.dataRange = JSON.parse(this.attrCur.dataRange);
    this.visibleDialog = true;
  }

  //保存自定义属性
  handleAddTypeSubmit() {
    this.closeDialog();
    this.getAttrsList();
  }

  //删除自定义属性
  deleteAttr(attrId: string) {
    let _this = this;
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(Url.deleteBaseTypeAttr + "/" + attrId).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              this.getAttrsList();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  //上移自定义属性
  upAttrSort(attr: any, index: any) {
    const idx = index + this.searchDTO.size * (this.searchDTO.current - 1);
    if (idx > 0) {
      this.$http.post(Url.upAttrSort, attr).then((res: any) => {
        if (res.data.status) {
          this.$message.success("操作成功");
          this.getAttrsList();
        } else {
          this.$message.error(res.data.message);
        }
      });
    } else {
      this.$message.info("已经是第一条");
    }
  }

  //下移自定义属性
  downAttrSort(attr: any, index: any) {
    const idx = index + this.searchDTO.size * (this.searchDTO.current - 1);
    if (idx < this.searchDTO.total - 1) {
      this.$http.post(Url.downAttrSort, attr).then((res: any) => {
        if (res.data.status) {
          this.$message.success("操作成功");
          this.getAttrsList();
        } else {
          this.$message.error(res.data.message);
        }
      });
    } else {
      this.$message.info("已经是最后一条");
    }
  }

  created() {
    this.getBaseTypeList();
  }
}
