 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

@Component({
  components: {}
})
export default class AddType extends Vue {
  @Prop({ type: String, default: "添加基础属性类型" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  addForm: any = {
    id: "",
    code: "",
    baseTypeName: "",
    sort: 1
  };
  formRules: any = {
    code: { required: true, trigger: "blur", message: "请输入编码" },
    baseTypeName: { required: true, trigger: "blur", message: "请输入类型名称" }
  };

  closeDialog() {
    this.$emit("close");
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        this.$emit("submit", this.addForm);
      } else {
        return false;
      }
    });
  }
}
