 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";
const AMap: any = (window as any).AMap;

@Component({
  components: {}
})
export default class AddBaseType extends Vue {
  @Prop({ type: String, default: "添加应用分类" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  //自定义属性类型
  itemOptions: Array<any> = [
    {
      value: "select",
      label: "单项选择"
    },
    {
      value: "multiSelect",
      label: "多项选择"
    },
    {
      value: "number",
      label: "数值"
    },
    {
      value: "image",
      label: "图片"
    },
    {
      value: "text",
      label: "文本"
    },
    {
      value: "location",
      label: "定位"
    },
    {
      value: "annex",
      label: "附件"
    }
  ];

  addDialog = false;
  addForm: any = {
    id: "",
    baseTypeId: "",
    code: "",
    attrName: "",
    attrType: "",
    sort: "",
    dataRange: {
      options: [],
      multiSelectedItems: []
    }
  };
  formRules: any = {
    attrName: { required: true, trigger: "blur", message: "请输入数据项名称" },
    code: { required: true, trigger: "blur", message: "请输入编码" },
    attrType: { required: true, trigger: "blur", message: "请选择数据项类型" }
  };
  loading = false;

  closeDialog() {
    this.$emit("close");
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        switch (this.addForm.attrType) {
          case "select":
          case "multiSelect":
            if (this.addForm.dataRange.options.length === 0) {
              this.$message.error("请添加选项");
              return;
            }
            break;
          case "number":
            if (
              this.addForm.dataRange.numMin === "" ||
              this.addForm.dataRange.numMin === undefined ||
              this.addForm.dataRange.numMax === "" ||
              this.addForm.dataRange.numMax === undefined
            ) {
              this.$message.error("请设置最大值或最小值");
              return;
            }
            break;
          case "image":
            if (this.addForm.dataRange.imgMaxNum === "" || this.addForm.dataRange.imgMaxNum === undefined) {
              this.$message.error("请设置图片的最大张数");
              return;
            }
            break;
        }
        this.loading = true;
        let url = this.addForm.id ? Url.updateAttr : Url.addAttr;
        let params = JSON.parse(JSON.stringify(this.addForm));
        params.dataRange = JSON.stringify(this.addForm.dataRange);
        this.$http
          .post(url, params)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$emit("submit");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }

  //===自定义属性弹窗相关================
  removeOptionItem(index: number) {
    this.addForm.dataRange.options.splice(index, 1);
  }

  addOptionItem() {
    if (this.addForm.attrType === "select" || this.addForm.attrType === "multiSelect") {
      this.addForm.dataRange.options.push({
        content: "",
        defaultChosen: false
      });
    } else {
      this.addForm.dataRange.options.push({});
    }
  }

  // 数据项类型修改
  typeChange() {
    if (this.addForm.attrType === "select" || this.addForm.attrType === "multiSelect") {
      this.addForm.dataRange.options = [];
      this.addForm.dataRange.multiSelectedItems = [];
    } else if (this.addForm.attrType === "number") {
      this.$set(this.addForm.dataRange, "numMin", 0);
      this.$set(this.addForm.dataRange, "numMax", 10);
      this.$set(this.addForm.dataRange, "unit", "");
    } else if (this.addForm.attrType === "text") {
      this.$set(this.addForm.dataRange, "presetText", "");
    } else if (this.addForm.attrType === "image") {
      this.$set(this.addForm.dataRange, "imgMaxNum", 1);
    }
  }

  handleCheck(index: number) {
    if (this.addForm.dataRange.options[index].defaultChosen && this.addForm.attrType === "select") {
      for (let i in this.addForm.dataRange.options) {
        this.addForm.dataRange.options[i].defaultChosen = false;
      }
      this.addForm.dataRange.options[index].defaultChosen = true;
    }
  }
}
